
import { defineComponent, onBeforeUnmount } from "vue";
import Render from "./render.vue";
import { IRoute, Router } from "@/components/Previewer/router";
import { cloneDeep } from "lodash";

export default defineComponent({
  name: "previewer",
  inheritAttrs: false,
  props: {
    rem: {
      type: Boolean,
      default: true,
    },
    pages: {
      type: Array,
      default: () => [],
    },
    modelValue: String,
    homePageId: {
      type: String,
    },
  },
  emits: ["update:homePageId"],
  components: { Render },
  setup(props, { emit }) {
    const router = new Router({
      routes: cloneDeep(props.pages) as IRoute[],
      homePage: props.homePageId,
      mode: "hash",
      onChange($router) {
        emit("update:homePageId", $router.current?.id);
      },
    });
    onBeforeUnmount(() => {
      console.log("销毁");
      router.destroy();
    });
    return {
      components: router.renderComponents,
      setPath(flag: string) {
        router.setPath(flag);
      },
    };
  },
});
